.hamburger {
  display: none;
  position: relative;
  top: 0;
  right: 0;
  z-index: 5;
  width: 50px;
  height: 50px;
  border-radius: 50%;
  outline: none;
  transition: all 0.3s ease-in-out;
  box-shadow: 0 0 2rem transparent;
}
@media screen and (max-width: 768px) {
  .hamburger {
    display: block;
  }
}
.hamburger:hover, .hamburger:focus {
  box-shadow: 0 0 0.5rem rgba(161, 161, 161, 0.5);
}

.hamburger__line {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  margin: auto;
  width: 22px;
  height: 2px;
  border-radius: 2px;
  background-color: #333333;
  transition: inherit;
}
.hamburger__line::before, .hamburger__line::after {
  position: absolute;
  content: "";
  display: block;
  width: 100%;
  height: 100%;
  border-radius: inherit;
  background-color: inherit;
  transition: inherit;
}

.drawer-background {
  position: fixed;
  z-index: 3;
  width: 100vw;
  top: 0;
  right: 0;
  bottom: 0;
  overflow: hidden;
  height: 100vh;
  background-color: #000;
  color: #fff;
  visibility: hidden;
  opacity: 0;
}

.is-drawerActive .sp-global-menu {
  transform: translateX(0px);
  transition: 0.3s;
}

.is-drawerActive .drawer-background {
  visibility: visible;
  opacity: 0.8;
  -webkit-transition: 0.3s;
  transition: 0.3s;
}

.hamburger__line::before {
  top: -7px;
}

.hamburger__line::after {
  top: 7px;
}

.hamburger[aria-expanded=true] {
  border: 1px solid #fff !important;
}

.hamburger[aria-expanded=true] .hamburger__line {
  background-color: transparent;
}

.hamburger[aria-expanded=true] .hamburger__line::before, .hamburger[aria-expanded=true] .hamburger__line::after {
  top: 0;
  background-color: #fff;
}

.hamburger[aria-expanded=true] .hamburger__line::before {
  transform: rotate(45deg);
}

.hamburger[aria-expanded=true] .hamburger__line::after {
  transform: rotate(-45deg);
}

